/* Header
------------------------------------------------------------*/
.c-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    position: relative;
    background: rgba(255,255,255,0.9);
}
/* Main menu
------------------------------------------------------------*/
.c-gnavi{
    min-width: 75px;
    max-width: 75px;
    height: 100vh;
    min-height: 570px;
    background-color: #fff;
    display: flex;
    z-index: 9;
    box-shadow: 0 2px 4px rgba(15,34,58,.12);
    .c-gnavi__box{
        text-align: center;
        .logo{
            line-height: 70px;
            display: block;
            img{
                height: 30px;
            }
        }
    }
    &__nav{
        .c-gnavi__item{
            margin: 7px 0;
            .nav-link{
                text-align: center;
                font-size: 24px;
                color: #878a92;
                width: 56px;
                height: 56px;
                line-height: 56px;
                margin: 0 auto;
                border-radius: 8px;
                padding: 0;
                &.active{
                    background-color: #f7f7ff;
                    color: #7269ef;
                }
            }
        }
        .c-gnavi__user{
            height: 36px;
            width: 36px;
            background-color: #f0eff5;
            padding: 3px;
            position: relative;
            display: inline-block;
        }
    }
}