/*------------------------------------------------------------
Css of chat page
------------------------------------------------------------*/
.p-chat{
    &1{
    	height: 100vh;
    	background-color: #f5f7fb;
    	&__leftsidebar{
    		@include breakpoint (pc){
    			min-width: 380px;
    			max-width: 380px;
    		}
    		.c-user1{
    			background-color: #e6ebf5;
    			padding: 8px;
    			border-radius: 8px;
    			text-align: center;
    			margin-top: 16px;
    			display: block;
    			.c-user1__img{
    				position: absolute;
    				top: 0;
    				left: 0;
    				right: 0;
    				height: 2.2rem;
    				width: 2.2rem;
    				&.online{
    					.c-user1__status{
	    					background-color: #06d6a0;
	    				}
    				}
    			}
    			&__title{
    				align-items: center;
    				background-color: #7269ef;
    				color: #fff;
    				display: flex;
    				font-weight: 500;
    				height: 100%;
    				justify-content: center;
    				width: 100%;
    				background-color: rgba(114,105,239,.25)!important;
    			}
    			&__status{
    				width: 10px;
					height: 10px;
					background-color: #adb5bd;
					border-radius: 50%;
					border: 2px solid #fff;
					position: absolute;
					right: 0;
					bottom: 0;
    			}
    		}
		}
    }
}